<template>
  <div class="organization-information">
    <div class="organization-information-title" v-if="orgData.id">
      <span></span>
      <p>{{orgData.name}}</p>
    </div>
    <el-form class="float-right" :inline="true" v-if="orgData.id">
      <el-form-item v-if="isNoEdit">
        <el-button type="primary" size="small" @click="isNoEdit=false">编辑</el-button>
        <el-button type="primary" size="small" @click="deleteHandle">删除</el-button>
        <el-button type="primary" size="small" @click='addSubordinateHandle'>添加下级机构</el-button>
        <el-button type="primary" size="small" @click="subordinateSortHandle">下级机构排序</el-button>
      </el-form-item>
      <el-form-item v-else>
        <el-button type="primary" size="small" @click="saveHandle">保存</el-button>
        <!-- <el-button type="primary" size="small" @click="isNoEdit=true">取消</el-button> -->
      </el-form-item>
    </el-form>
    <div class="clearfix"></div>
    <el-form class="organization-information-form" label-width="200px" label-position="left" :disabled='isNoEdit'>
      <el-row>
        <el-col :span="12">
          <el-form-item label="机构标识" prop="b0194">
            <el-radio-group v-model="inforKeyRadio">
              <el-radio v-for="item in inforKey" :label="item.id" :key="item.id">{{item.name}}</el-radio>
            </el-radio-group>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="机构全称" prop="b0101">
            <el-input v-model="form.b0101"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="机构简称" prop="b0104">
            <el-input v-model="form.b0104"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="机构编码" prop="b0111">
            <el-input v-model="form.b0111"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row v-if="inforKeyRadio==1">
        <el-col :span="12">
          <el-form-item label="所在政区" prop="b0117">
            <el-input v-model="form.b0117"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="隶属关系" prop="b0124">
            <el-input v-model="form.b0124"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12" v-if="inforKeyRadio==1">
          <el-form-item label="机构类别" prop="b0131">
            <el-input v-model="form.b0131"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="inforKeyRadio==1?12:24" v-if="inforKeyRadio!=3">
          <el-form-item label="机构级别" prop="b0127">
            <el-input v-model="form.b0127"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row v-if="inforKeyRadio==1">
        <el-col :span="12">
          <el-form-item label="机构成立批准日期" prop="b0164">
            <el-date-picker v-model="form.b0164" type="date" format="yyyy.MM.dd" value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="机构成立批准文号" prop="b0167">
            <el-input v-model="form.b0167"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-form-item label="备注" prop="b0197">
            <el-input v-model="form.b0197"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <!-- 弹窗，下级机构排序 -->
    <subordinate-sort v-if="subordinateSortVisible" ref='subordinateSort' @refreshDataList="refreshDataList"></subordinate-sort>
  </div>
</template>
<script>
  import subordinateSort from './subordinate-sort.vue'
  export default{
    components:{
      subordinateSort
    },
    data(){
      return{
        form:{},
        inforKeyRadio:'1',
        inforKey:[
          {id:'1',name:'法人机构'},
          {id:'2',name:'内设机构'},
          {id:'3',name:'机构分组'}
        ],
        isNoEdit:true,
        subordinateSortVisible:false,
        orgData:{id:'',name:''},
        addSubordinate:false
      }
    },
    methods:{
      init(org){
        this.orgData.id=org.id;
        this.orgData.name=org.name;
        this.isNoEdit=true;
        this.addSubordinate=false;
        this.$http({
          url: this.$http.adornUrl(this.API.getOrgDetailsB01),
          method: 'post',
          data: this.$http.adornData({
            b0100:org.id
          })
        }).then(({data}) => {
          if(data && data.code===0){
            this.form=data.data;
            this.inforKeyRadio=this.form.b0194;
          }else{
            this.form={};
            this.inforKeyRadio='1';
            this.$message.error(data.msg);
          }
        });
      },
      addSubordinateHandle(){
        this.addSubordinate=true;
        this.form={};
        this.inforKeyRadio='1';
        this.isNoEdit=false;
      },
      saveHandle(){
        this.form['b0100']=this.addSubordinate?null:this.orgData.id;
        this.form['b0100p']=this.addSubordinate?this.orgData.id:this.form.b0100p;
        this.form['b0194']=this.inforKeyRadio;
        this.$http({
          url: this.$http.adornUrl(this.API[this.addSubordinate?'saveOrgB01':'updateOrgB01']),
          method: 'post',
          data: this.$http.adornData(this.form)
        }).then(({data}) => {
          if(data && data.code===0){
            this.$message({
              message: '操作成功',
              type: 'success',
              duration: 1500,
              onClose: () => {
                if(!this.addSubordinate) return;
                this.$emit('refreshDataList')
              }
            })
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      deleteHandle(){
        this.$confirm('确定删除机构,是否继续？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http({
            url: this.$http.adornUrl(this.API.deleteOrgB01),
            method: 'post',
            data: this.$http.adornData({
              b0100:this.orgData.id
            })
          }).then(({data}) => {
            if(data && data.code===0){
              this.$message({
                message: '操作成功',
                type: 'success',
                duration: 1500,
                onClose: () => {
                  this.$emit('refreshDataList')
                }
              })
            }else{
              this.$message.error(data.msg);
            }
          });
        }).catch(() => {})
      },
      subordinateSortHandle(){
        this.subordinateSortVisible=true;
        this.$nextTick(() => {
          this.$refs.subordinateSort.init(this.orgData.id)
        })
      },
      refreshDataList(){
        this.$emit('refreshDataList')
      }
    }
  }
</script>

<style lang="scss" scoped>
  .organization-information{
    &-title{
      float: left;
      padding-left: 10px;
      color: #f0992b;
      margin-top: 7px;
      span{
        display: inline-block;
        width:10px;
        height: 10px;
        background: rgba(#f0992b,0.6);
        box-shadow: 0px 0px 6px 0px #f0992b;border-radius: 50%;}
      p{display: inline-block; padding-left: 10px;}
    }
    .el-form--inline .el-form-item{margin-bottom: 10px;margin-top: 10px;}
    &-form{
      padding: 0 6px 0 0;
      .el-form-item{
        border:1px solid #D1CECE;
        margin: 0 4px 10px;
        /deep/ .el-form-item__label{padding-left: 6px;border-right: 1px solid #D1CECE;background: #F9F9F9;}
        /deep/ .el-input__inner{border: none}
        /deep/ .el-radio-group{padding-left: 10px;}
      }
      /deep/ .el-input.is-disabled .el-input__inner{background: none;}
      /deep/ .el-date-editor.el-input, .el-date-editor.el-input__inner{width: 100%;
        .el-input__prefix{left: inherit;right: 5px;}
      }
      /deep/ .el-input--prefix .el-input__inner{padding-left: 15px;}
    }
  }
</style>



